import common from "../../common/common.js"
export default {
	data() {
		return {
			userIsLogin: '',
			globalUser: ''
		}
	},
	onShow() {
		let me = this
		// 用户状态切换
		let globalUser = me.getGlobalUser('globalUser')
		this.globalUser = globalUser
	},
	methods: {
		cleanStorage() {
			uni.clearStorageSync()
			uni.showToast({
				title:"清理缓存成功",
				mask:false,
				duration: 1500,
			})
			setTimeout(()=>{
				uni.switchTab({
					url: '../me/me'
				})
			},1500)
		},
		logout() {
			// 请求轮播图数据
			common.urlRequest('/user/logout',{userId:this.globalUser.id},'POST',(res)=>{
				console.log(res.status)
				if (res.status === 200) {
					// 如果服务器返回200，代表用户在服务器退出登录成功
					uni.removeStorageSync('globalUser')
					uni.switchTab({
						url:"../me/me"
					})
				}
				// this.carouselList = res.data
			})
		},
		operator() {
			let me = this
			uni.showActionSheet({
				itemList: ["查看我的头像","从相册选择上传"],
				success(res) {
					let index = res.tapIndex
					if (index == 0) {
						// 预览头像
						let faceArr = []
						faceArr.push(me.globalUser.faceImage)
						uni.previewImage({
							urls: faceArr,
							current: faceArr[0]
						})
					} else if (index == 1) {
						// 选择上传头像
						uni.chooseImage({
							count: 1,
							sizeType:["compressed"],
							sourceType: ["album"],
							success(res) {
								// 获得临时路径
								let tempFilePath = res.tempFilePaths[0]





								

								uni.navigateTo({
									url:`../faceCrop/faceCrop?tempFilePath=${tempFilePath}`
								})

							}
						})
					}
				}
			})
		},
		modifyNickname() {
			uni.navigateTo({
				url:'../modifyNickname/modifyNickname'
			})
		},
		modifyBirthday() {
			uni.navigateTo({
				url: "../modifyBirthday/modifyBirthday"
			})
		},
		modifySex() {
			uni.navigateTo({
				url: "../setSex/setSex"
			})
		}
	}
}
